
import { defineComponent } from "vue";

export default defineComponent({
  name: "CityChoice",

  emits: ["selectCity"],

  props: {
    option: { type: String, required: true },
  },

  data() {
    return {
      disabled: false,
    };
  },

  methods: {
    click() {
      this.$emit("selectCity");
      this.disabled = true;
    },
  },

  watch: {
    option() {
      this.disabled = false;
    },
  },
});
